// Module
___CSS_LOADER_EXPORT___.push([module.id, ".form-control-input{background-color:transparent;background-image:url(\"data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgZmlsbD0ibm9uZSIgc3Ryb2tlPSIjNjY2IiBzdHJva2Utd2lkdGg9IjIiIHN0cm9rZS1saW5lY2FwPSJyb3VuZCIgc3Ryb2tlLWxpbmVqb2luPSJyb3VuZCI+PGNpcmNsZSBjeD0iMTEiIGN5PSIxMSIgcj0iOCIvPjxwYXRoIGQ9Im0yMSAyMS00LTQiLz48L3N2Zz4=\");background-position:12px left;background-repeat:no-repeat;border:none;box-sizing:inherit;color:#fff;flex:1;font-size:16px;line-height:1.5;margin:0;padding:0 0 0 48px;position:relative;width:100%}.form-control-input:focus,.form-control-input[aria-expanded=true]{border:none;color:#fff;outline:none}[data-position=below] .form-control-input[aria-expanded=true]{border-bottom-color:transparent}[data-position=above] .form-control-input[aria-expanded=true]{z-index:2}.form-control[data-loading=true]:after{-webkit-animation:rotate 1s linear infinite;animation:rotate 1s linear infinite;border:3px solid rgba(0,0,0,.12);border-radius:20%;border-right-color:rgba(0,0,0,.48);content:\"\";height:20px;position:absolute;right:12px;top:50%;transform:translateY(-50%);width:20px}.form-control-result-list{background:#fff;border:1px solid rgba(0,0,0,.12);box-shadow:0 2px 2px rgba(0,0,0,.16);box-sizing:border-box;list-style:none;margin:-13px;max-height:296px;overflow-y:auto;padding:0;width:85%}[data-position=above] .form-control-result-list,[data-position=below] .form-control-result-list{border-radius:0 0 2px 2px;border-top-color:transparent;margin-top:-1px;padding-bottom:1px}.form-control-result{background-image:url(\"data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgZmlsbD0ibm9uZSIgc3Ryb2tlPSIjY2NjIiBzdHJva2Utd2lkdGg9IjIiIHN0cm9rZS1saW5lY2FwPSJyb3VuZCIgc3Ryb2tlLWxpbmVqb2luPSJyb3VuZCI+PGNpcmNsZSBjeD0iMTEiIGN5PSIxMSIgcj0iOCIvPjxwYXRoIGQ9Im0yMSAyMS00LTQiLz48L3N2Zz4=\");background-position:12px;background-repeat:no-repeat;color:#000;cursor:default;padding:12px 12px 12px 48px}.form-control-result:hover,.form-control-result[aria-selected=true]{background-color:rgba(0,0,0,.06)}", "",{"version":3,"sources":["webpack://./resources/js/components/Sales/_partials/FinalizeSale.vue"],"names":[],"mappings":"AA+bA,oBAWA,4BAAA,CACA,8UAAA,CAEA,6BAAA,CADA,2BAAA,CAZA,WAAA,CAIA,kBAAA,CAKA,UAAA,CADA,MAAA,CAFA,cAAA,CACA,eAAA,CAJA,QAAA,CADA,kBAAA,CAGA,iBAAA,CAJA,UAaA,CAEA,kEAEA,WAAA,CACA,UAAA,CACA,YAEA,CAEA,8DACA,+BAEA,CAEA,8DAGA,SACA,CAGA,uCAWA,2CAAA,CAAA,mCAAA,CARA,gCAAA,CACA,iBAAA,CADA,kCAAA,CAFA,UAAA,CAKA,WAAA,CACA,iBAAA,CACA,UAAA,CACA,OAAA,CACA,0BAAA,CALA,UAOA,CAEA,0BAQA,eAAA,CALA,gCAAA,CAOA,oCAAA,CALA,qBAAA,CAIA,eAAA,CARA,YAAA,CAKA,gBAAA,CACA,eAAA,CAHA,SAAA,CAFA,SASA,CAEA,gGAIA,yBAAA,CADA,4BAAA,CADA,eAAA,CAGA,kBACA,CAUA,qBAIA,8UAAA,CAEA,wBAAA,CADA,2BAAA,CAJA,UAAA,CACA,cAAA,CACA,2BAIA,CAEA,oEAEA,gCACA","sourcesContent":["<template>\n<div class=\"modal fade\" id=\"staticBackdropSale\" data-backdrop=\"static\" data-keyboard=\"false\" tabindex=\"-1\" aria-labelledby=\"staticBackdropSaleLabel\" aria-hidden=\"true\">\n    <div class=\"modal-dialog modal-xl\">\n        <div class=\"modal-content\">\n            <div class=\"modal-header\">\n                <h5 class=\"modal-title\" id=\"staticBackdropSaleLabel\">Finalizar venda</h5>\n                <button type=\"button\" class=\"close\" data-dismiss=\"modal\" aria-label=\"Close\">\n                    <span aria-hidden=\"true\">&times;</span>\n                </button>\n            </div>\n            <div class=\"modal-body\">\n                <div class=\"row\">\n                    <div class=\"col-md-6 border-right-light\">\n                        <div class=\"card\">\n                            <div class=\"card-header\">\n                                <strong>Pagamento</strong>\n                            </div>\n                            <div class=\"card-body\">\n                                <div class=\"form-row\">\n                                    <div class=\"input-group input-group-prepend\">\n                                        <div class=\"btn btn-outline-secondary input-group-prepend custom-control custom-switch pl-5\">\n                                            <input @click=\"isDelivery\" type=\"checkbox\" class=\"custom-control-input\" id=\"mode\">\n                                            <label class=\"custom-control-label\" for=\"mode\">Entrega</label>\n                                        </div>\n\n                                        <autocomplete v-show=\"delivery\"  style=\"z-index: 100; width:76%;\"\n                                            :baseClass=\"'form-control'\"\n                                            :search=\"getDeliverymen\"\n                                            :get-result-value=\"getDeliverymanResultValue\"\n                                            @submit=\"handleSubmitDeliveryman\"\n                                            placeholder=\"Pesquise pelo nome ou cpf do entregador\"\n                                            aria-label=\"Pesquise pelo nome ou cpf do entregador\"\n                                        ></autocomplete>\n\n                                        <autocomplete v-show=\"!delivery\"  style=\"z-index: 100; width:76%;\"\n                                            :baseClass=\"'form-control'\"\n                                            :search=\"getTables\"\n                                            :get-result-value=\"getTableResultValue\"\n                                            @submit=\"handleSubmitTable\"\n                                            placeholder=\"Pesquise pelo nome da mesa\"\n                                            aria-label=\"Pesquise pelo nome da mesa\"\n                                        ></autocomplete>\n                                    </div>\n                                </div>\n\n                                <div class=\"form-row mt-md-3\">\n                                    <div class=\"input-group col-md-7\">\n                                        <div class=\"input-group-append\">\n                                            <button class=\"btn btn-outline-secondary\"\n                                                data-toggle=\"modal\"\n                                                data-target=\"#staticBackdropAddPayment\"\n                                                title=\"Adicionar forma de pagamento\"\n                                            >\n                                                <i class=\"fas fa-plus-square\"></i> NOVA\n                                            </button>\n                                        </div>\n                                        <select v-model=\"order.form_payment_id\" class=\"custom-select input-group-prepend\">\n                                            <option value=\"\" selected>Forma de pagamento * </option>\n                                            <option v-for=\"(payment, index) in formPayments\" :key=\"index\" :value=\"payment.id\">\n                                                {{payment.name}}\n                                            </option>\n                                        </select>\n                                    </div>\n\n                                    <div class=\"input-group col-md-5\">\n                                        <div class=\"input-group-prepend\">\n                                            <button class=\"btn btn-outline-secondary\" type=\"button\">\n                                                Desconto\n                                            </button>\n                                        </div>\n                                        <money v-model=\"order.total_discount\" v-bind=\"money\" @keyup.native=\"amountPaid\" class=\"form-control\"></money>\n                                    </div>\n                                </div>\n\n                                <div class=\"form-row mt-md-3\">\n                                    <div class=\"input-group col-md-6\">\n                                        <div class=\"input-group-prepend\">\n                                            <button class=\"btn btn-outline-secondary\" type=\"button\">\n                                                {{titleDelivery}}\n                                            </button>\n                                        </div>\n                                        <money v-model=\"order.shipping\" v-bind=\"money\" @keyup.native=\"amountPaid\" class=\"form-control\"></money>\n                                    </div>\n\n                                    <div class=\"input-group col-md-6\">\n                                        <div class=\"input-group-prepend\">\n                                            <span class=\"input-group-text\"> VALOR PAGO * </span>\n                                        </div>\n                                        <money v-model=\"order.total_paid\" v-bind=\"money\" class=\"form-control col-12 input-group-append\"></money>\n                                    </div>\n\n                                    <div class=\"input-group mt-md-3 col-md-12\">\n                                        <textarea v-model=\"order.comment\" placeholder=\"Observaes: massa fina, sem cebola, etc\" class=\"form-control\"></textarea>\n                                    </div>\n                                </div>\n\n                                <div class=\"card mt-md-3 p-md-0\">\n                                    <div class=\"card-header\">\n                                        <strong>Itens do pedido</strong>\n                                    </div>\n                                    <div style=\"height: 200px;\" class=\"card-body table-responsive p-md-0\">\n                                        <table class=\"table table-sm table-condensed table-dark table-striped table-hover table-borderless align-middle table-head-fixed\">\n                                            <thead>\n                                                <tr>\n                                                    <th>Produtos</th>\n                                                    <th>Preo</th>\n                                                    <th>Quant.</th>\n                                                    <th>Desc.</th>\n                                                    <th>Subtotal</th>\n                                                </tr>\n                                            </thead>\n                                            <tbody>\n                                                <tr v-for=\"(product, index) in order.products\" :key=\"index\">\n                                                    <td>{{ product.title }}</td>\n                                                    <td>{{ product.price }}</td>\n                                                    <td>{{ product.qty }}</td>\n                                                    <td>{{ product.discount | currency('R$ ', 2, {decimalSeparator: ','}) }}</td>\n                                                    <td>\n                                                        {{ sumSubtotal(product) | currency('R$ ', 2, {decimalSeparator: ','}) }}\n                                                    </td>\n                                                </tr>\n                                            </tbody>\n                                        </table>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                    <div class=\"col-md-6\">\n                        <div class=\"card\">\n                            <div class=\"card-header\">\n                                <strong>{{client.name}}</strong>\n                            </div>\n                            <div class=\"card-body row\">\n                                <p class=\"col-12\">\n                                    {{client.phone}} | {{client.email}} | {{client.cpf}}\n                                </p>\n\n                                <address class=\"col-12\">\n                                    <textarea v-model=\"address\" @keyup=\"confirmAddress\" class=\"form-control\"></textarea>\n                                    <a href=\"#\" @click.prevent=\"formatAddress\">\n                                        Usar endereo cadastrado?\n                                    </a>\n                                </address>\n                            </div>\n                        </div>\n\n                        <div class=\"col-12 float-right\">\n                            <p class=\"lead\">Resumo</p>\n\n                            <div class=\"table-responsive table-sm\">\n                                <table class=\"table\">\n                                    <tr>\n                                        <th style=\"width:50%\">Valor do pedido:</th>\n                                        <td>\n                                            <span class=\"float-right\">\n                                                {{ order.total | currency('R$ ', 2, {decimalSeparator: ','})}}\n                                            </span>\n                                        </td>\n                                    </tr>\n                                    <tr>\n                                        <th>{{titleDelivery}}:</th>\n                                        <td>\n                                            <span class=\"float-right text-success\">\n                                                {{ order.shipping | currency('R$ ', 2, {decimalSeparator: ','})}}\n                                            </span>\n                                        </td>\n                                    </tr>\n                                    <tr>\n                                        <th>Desconto total</th>\n                                        <td>\n                                            <span class=\"float-right text-danger\">\n                                                {{order.total_discount | currency('R$ ', 2, {decimalSeparator: ','}) }}\n                                            </span>\n                                        </td>\n                                    </tr>\n                                    <tr class=\"middle\">\n                                        <th>Total (Pedido + Entrega - Desconto):</th>\n                                        <td>\n                                            <span class=\"float-right text-success\">\n                                                {{ total | currency('R$ ', 2, {decimalSeparator: ','}) }}\n                                            </span>\n                                        </td>\n                                    </tr>\n                                    <tr>\n                                        <th>Valor Pago:</th>\n                                        <td>\n                                            <span class=\"float-right text-danger\">\n                                                {{order.total_paid | currency('R$ ', 2, {decimalSeparator: ','}) }}\n                                            </span>\n                                        </td>\n                                    </tr>\n                                    <tr>\n                                        <th>Troco:</th>\n                                        <td><span class=\"float-right\">{{ totalChange | currency('R$ ', 2, {decimalSeparator: ','}) }}</span></td>\n                                    </tr>\n                                </table>\n                            </div>\n                        </div>\n                        <div class=\"row justify-content-center\">\n                            <button type=\"button\" class=\"col-md-3 btn btn-secondary mr-1\" data-dismiss=\"modal\">\n                                <i class=\"fas fa-chevron-circle-left\"></i> Voltar\n                            </button>\n\n                            <button v-if=\"validateFields\" @click=\"confirmOrder\" type=\"button\" class=\"col-md-8 btn btn-success btn-block float-right\">\n                                <i class=\"far fa-credit-card\"></i> Finalizar Venda\n                            </button>\n                            <button v-else disabled type=\"button\" class=\"col-md-8 btn btn-success btn-block float-right\">\n                                <i class=\"far fa-credit-card\"></i> Finalizar Venda\n                            </button>\n                        </div>\n\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n\n    <add-payment @formPaymentSaved=\"formPaymentSaved\"></add-payment>\n\n</div>\n</template>\n\n<script>\nimport Autocomplete from '@trevoreyre/autocomplete-vue'\nimport AddPayment from './AddPayment.vue'\nimport { Money } from 'v-money'\nexport default {\n    props: {\n        order: {\n            type: [Array, Object],\n            required: true\n        },\n\n        client: {\n            type: [Array, Object],\n            required: true,\n        },\n    },\n\n    created() {\n        // this.address = this.client.addresses\n        if (this.formPayments == '') {\n            this.getFormPayments()\n        }\n    },\n\n    computed: {\n        total() {\n            return this.order.total + this.order.shipping - this.order.total_discount\n        },\n\n        totalChange() {\n            let totalChange = this.order.total_paid - this.total\n            return this.order.total_change = this.round(totalChange, 2)\n        },\n        validateFields () {\n            if (this.address == '' ||\n                this.order.form_payment_id == 0 ||\n                this.order.total_paid == 0\n            ) {\n                return false\n            }\n\n            console.log('deliv', this.order.deliveryman, 'tab', this.order.table)\n\n            if (this.delivery) {\n                return this.order.deliveryman == '' ? false : true\n            } else {\n                return this.order.table == '' ? false : true\n            }\n        },\n    },\n\n    data() {\n        return {\n            address: '',\n            formPayments: '',\n            delivery: false,\n            titleDelivery: 'Taxa',\n            tables: [],\n            money: {\n                decimal: ',',\n                thousands: '.',\n                prefix: 'R$ ',\n                suffix: '',\n                precision: 2,\n                masked: false /* doesn't work with directive */\n            }\n        }\n    },\n\n    methods: {\n        confirmOrder () {\n            axios.post(`/api/v1/orders`, this.order)\n                .then(res => {\n                    console.log('res', res)\n                    this.$emit('costumerSavedOrder')\n                    this.closeModaBootstrap()\n                    // this.resetOrder()\n                    // this.$vToastify.success('Pedido feito.', 'Uhuuu!')\n                })\n                .catch(error => {\n                    const errorResponse = error.response\n\n                    console.log('errorResponse', errorResponse)\n\n                    if (errorResponse.status === 422) {\n                        this.errors = Object.assign(this.errors, errorResponse.data.errors)\n\n                        this.$vToastify.error('Dados invlidos, verifique novamente', 'Ooops!')\n\n                        setTimeout(() => this.resetClient(), 4000)\n\n                        return;\n                    }\n                    this.$vToastify.error('Falha ao Registrar o cliente', 'Ooops!')\n                })\n                // .finally(this.loading = false)\n        },\n\n        getFormPayments() {\n            axios.get(`/api/v1/form-payments`)\n                .then(res => {\n                    this.formPayments = res.data.data\n                })\n                .catch(err => this.$vToastify.error('Falha ao finalizar venda', 'Ooops!'))\n        },\n\n        getTables (input) {\n            const url = `/api/v1/tables?filter=${input}`\n\n            return new Promise(resolve => {\n                if (input.length < 3) {\n                    return resolve([])\n                }\n\n                fetch(url)\n                .then(response => response.json())\n                .then(res => {\n                    resolve(res.data)\n                })\n                .catch(err => this.$vToastify.error('Falha ao finalizar venda', 'Ooops!'))\n            })\n        },\n\n        getTableResultValue(result) {\n            return result.name\n        },\n\n        handleSubmitTable(result) {\n            // this.table = result\n            this.order.table = result.identify\n        },\n\n        getDeliverymen (input) {\n            const url = `/api/v1/users/deliverymen?filter=${input}`\n\n            return new Promise(resolve => {\n                if (input.length < 3) {\n                    return resolve([])\n                }\n\n                fetch(url)\n                .then(response => response.json())\n                .then(res => {\n                    resolve(res.data)\n                })\n                .catch(error => this.$vToastify.error('Falha ao finalizar venda', 'Ooops!'))\n            })\n        },\n\n        getDeliverymanResultValue(result) {\n            return result.name\n        },\n\n        handleSubmitDeliveryman(result) {\n            // this.deliveryman = result\n            this.order.deliveryman = result.identify\n        },\n\n        confirmAddress () {\n            this.order.address = this.address\n        },\n\n        formPaymentSaved () {\n            this.getFormPayments()\n        },\n\n        amountPaid() {\n            this.order.total_paid = this.order.total + this.order.shipping - this.order.total_discount\n        },\n\n        isDelivery() {\n            this.delivery = this.delivery == true ? false : true\n            this.titleDelivery = this.delivery ? 'Entrega' : 'Taxa'\n            if (this.delivery)\n                this.order.table = null\n            else\n                this.order.deliveryman = null\n        },\n\n        sumSubtotal(product) {\n            let subtotal = this.round(product.price * product.qty - product.discount, 2);\n            return subtotal\n        },\n\n        round(num, places) {\n            if (!(\"\" + num).includes(\"e\")) {\n                return +(Math.round(num + \"e+\" + places) + \"e-\" + places);\n            } else {\n                let arr = (\"\" + num).split(\"e\");\n                let sig = \"\"\n                if (+arr[1] + places > 0) {\n                    sig = \"+\";\n                }\n\n                return +(Math.round(+arr[0] + \"e\" + sig + (+arr[1] + places)) + \"e-\" + places);\n            }\n        },\n\n        formatAddress() {\n            let address = this.client.addresses\n            if (address != undefined) {\n                const notes = address.notes != '' ? ' (' + address.notes + ') ' : ''\n                this.address = address.street.concat(notes, ', ', address.street_extra, ' - ', address.city, '/', address.state, '. ', address.post_code)\n            }\n            this.confirmAddress()\n        },\n\n        closeModaBootstrap () {\n            $('.modal-backdrop').hide(); // for black background\n            $('body').removeClass('modal-open'); // For scroll run\n            $('#modal').modal('hide');\n            $('#staticBackdropSale').css('display', 'none');\n        },\n    },\n\n    components: {\n        Money,\n        AddPayment,\n        Autocomplete\n    },\n}\n</script>\n\n<style>\n.form-control-input {\n  border: none;\n  width: 100%;\n  padding: 0px 0px 0px 48px;\n  margin: 0px;\n  box-sizing: inherit;\n  position: relative;\n  font-size: 16px;\n  line-height: 1.5;\n  flex: 1;\n  color: #fff;\n  background-color: transparent;\n  background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgZmlsbD0ibm9uZSIgc3Ryb2tlPSIjNjY2IiBzdHJva2Utd2lkdGg9IjIiIHN0cm9rZS1saW5lY2FwPSJyb3VuZCIgc3Ryb2tlLWxpbmVqb2luPSJyb3VuZCI+PGNpcmNsZSBjeD0iMTEiIGN5PSIxMSIgcj0iOCIvPjxwYXRoIGQ9Ik0yMSAyMWwtNC00Ii8+PC9zdmc+');\n  background-repeat: no-repeat;\n  background-position: 12px left;\n}\n\n.form-control-input:focus,\n.form-control-input[aria-expanded=\"true\"] {\n  border: none;\n  color: #fff;\n  outline: none;\n  /* box-shadow: 0 2px 2px rgba(0, 0, 0, 0.16); */\n}\n\n[data-position=\"below\"] .form-control-input[aria-expanded=\"true\"] {\n  border-bottom-color: transparent;\n  /* border-radius: 8px 8px 0 0; */\n}\n\n[data-position=\"above\"] .form-control-input[aria-expanded=\"true\"] {\n  /* border-top-color: transparent;\n  border-radius: 0 0 8px 8px; */\n  z-index: 2;\n}\n\n/* Loading spinner */\n.form-control[data-loading=\"true\"]::after {\n  content: \"\";\n  border: 3px solid rgba(0, 0, 0, 0.12);\n  border-right: 3px solid rgba(0, 0, 0, 0.48);\n  border-radius: 20%;\n  width: 20px;\n  height: 20px;\n  position: absolute;\n  right: 12px;\n  top: 50%;\n  transform: translateY(-50%);\n  animation: rotate 1s infinite linear;\n}\n\n.form-control-result-list {\n  margin: -13px;\n  width: 85%;\n  border: 1px solid rgba(0, 0, 0, 0.12);\n  padding: 0;\n  box-sizing: border-box;\n  max-height: 296px;\n  overflow-y: auto;\n  background: #fff;\n  list-style: none;\n  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.16);\n}\n\n[data-position=\"below\"] .form-control-result-list,\n[data-position=\"above\"] .form-control-result-list  {\n  margin-top: -1px;\n  border-top-color: transparent;\n  border-radius: 0 0 2px 2px;\n  padding-bottom: 1px;\n}\n\n/* [data-position=\"above\"] .form-control-result-list {\n  margin-bottom: -1px;\n  border-bottom-color: transparent;\n  border-radius: 8px 8px 0 0;\n  padding-top: 4px;\n} */\n\n/* Single result item */\n.form-control-result {\n  color: #000;\n  cursor: default;\n  padding: 12px 12px 12px 48px;\n  background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgZmlsbD0ibm9uZSIgc3Ryb2tlPSIjY2NjIiBzdHJva2Utd2lkdGg9IjIiIHN0cm9rZS1saW5lY2FwPSJyb3VuZCIgc3Ryb2tlLWxpbmVqb2luPSJyb3VuZCI+PGNpcmNsZSBjeD0iMTEiIGN5PSIxMSIgcj0iOCIvPjxwYXRoIGQ9Ik0yMSAyMWwtNC00Ii8+PC9zdmc+');\n  background-repeat: no-repeat;\n  background-position: 12px center;\n}\n\n.form-control-result:hover,\n.form-control-result[aria-selected=\"true\"] {\n  background-color: rgba(0, 0, 0, 0.06);\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
