___CSS_LOADER_EXPORT___.push([module.id, ".form-control-input{background-color:transparent;background-image:url(\"data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgZmlsbD0ibm9uZSIgc3Ryb2tlPSIjNjY2IiBzdHJva2Utd2lkdGg9IjIiIHN0cm9rZS1saW5lY2FwPSJyb3VuZCIgc3Ryb2tlLWxpbmVqb2luPSJyb3VuZCI+PGNpcmNsZSBjeD0iMTEiIGN5PSIxMSIgcj0iOCIvPjxwYXRoIGQ9Im0yMSAyMS00LTQiLz48L3N2Zz4=\");background-position:12px left;background-repeat:no-repeat;border:none;box-sizing:inherit;color:#fff;flex:1;font-size:16px;line-height:1.5;margin:0;padding:0 0 0 48px;position:relative;width:100%}.form-control-input:focus,.form-control-input[aria-expanded=true]{border:none;color:#fff;outline:none}[data-position=below] .form-control-input[aria-expanded=true]{border-bottom-color:transparent}[data-position=above] .form-control-input[aria-expanded=true]{z-index:2}.form-control[data-loading=true]:after{-webkit-animation:rotate 1s linear infinite;animation:rotate 1s linear infinite;border:3px solid rgba(0,0,0,.12);border-radius:20%;border-right-color:rgba(0,0,0,.48);content:\"\";height:20px;position:absolute;right:12px;top:50%;transform:translateY(-50%);width:20px}.form-control-result-list{background:#fff;border:1px solid rgba(0,0,0,.12);box-shadow:0 2px 2px rgba(0,0,0,.16);box-sizing:border-box;list-style:none;margin:-13px;max-height:296px;overflow-y:auto;padding:0;width:85%}[data-position=above] .form-control-result-list,[data-position=below] .form-control-result-list{border-radius:0 0 2px 2px;border-top-color:transparent;margin-top:-1px;padding-bottom:1px}.form-control-result{background-image:url(\"data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgZmlsbD0ibm9uZSIgc3Ryb2tlPSIjY2NjIiBzdHJva2Utd2lkdGg9IjIiIHN0cm9rZS1saW5lY2FwPSJyb3VuZCIgc3Ryb2tlLWxpbmVqb2luPSJyb3VuZCI+PGNpcmNsZSBjeD0iMTEiIGN5PSIxMSIgcj0iOCIvPjxwYXRoIGQ9Im0yMSAyMS00LTQiLz48L3N2Zz4=\");background-position:12px;background-repeat:no-repeat;color:#000;cursor:default;padding:12px 12px 12px 48px}.form-control-result:hover,.form-control-result[aria-selected=true]{background-color:rgba(0,0,0,.06)}", "",{"version":3,"sources":["webpack://./resources/js/components/Sales/PointSale.vue"],"names":[],"mappings":"AAoWA,oBAWA,4BAAA,CACA,8UAAA,CAEA,6BAAA,CADA,2BAAA,CAZA,WAAA,CAIA,kBAAA,CAKA,UAAA,CADA,MAAA,CAFA,cAAA,CACA,eAAA,CAJA,QAAA,CADA,kBAAA,CAGA,iBAAA,CAJA,UAaA,CAEA,kEAEA,WAAA,CACA,UAAA,CACA,YAEA,CAEA,8DACA,+BAEA,CAEA,8DAGA,SACA,CAGA,uCAWA,2CAAA,CAAA,mCAAA,CARA,gCAAA,CACA,iBAAA,CADA,kCAAA,CAFA,UAAA,CAKA,WAAA,CACA,iBAAA,CACA,UAAA,CACA,OAAA,CACA,0BAAA,CALA,UAOA,CAEA,0BAQA,eAAA,CALA,gCAAA,CAOA,oCAAA,CALA,qBAAA,CAIA,eAAA,CARA,YAAA,CAKA,gBAAA,CACA,eAAA,CAHA,SAAA,CAFA,SASA,CAEA,gGAIA,yBAAA,CADA,4BAAA,CADA,eAAA,CAGA,kBACA,CAUA,qBAIA,8UAAA,CAEA,wBAAA,CADA,2BAAA,CAJA,UAAA,CACA,cAAA,CACA,2BAIA,CAEA,oEAEA,gCACA","sourcesContent":["<template>\n<div class=\"card\">\n    <div class=\"card-header\">\n        <h5 class=\"card-title\">\n            <button class=\"btn btn-success\" @click=\"costumerSavedOrder\">\n                <i class=\"fas fa-cash-register\"></i> Nova venda\n            </button>\n        </h5>\n        <!-- <div class=\"card-tools\">\n            <div class=\"btn-group\">\n\n                <button type=\"button\" class=\"btn btn-light\">\n                    <i class=\"fas fa-wrench\"></i> Configurao\n                </button>\n            </div>\n        </div> -->\n    </div>\n    <!-- /.card-header -->\n    <div class=\"card-body\">\n        <div class=\"row\">\n            <div class=\"col-md-6 p-0\">\n                <div class=\"input-group col-md-12 mb-3\">\n                    <autocomplete style=\"z-index: 100; width:85%;\"\n                        :baseClass=\"'form-control'\"\n                        :search=\"getClients\"\n                        :get-result-value=\"getResultValue\"\n                        @submit=\"handleSubmit\"\n                        placeholder=\"Pesquise pelo nome, cdigo ou cpf do cliente\"\n                        aria-label=\"Pesquise pelo nome, cdigo ou cpf do cliente\"\n                    ></autocomplete>\n                    <div class=\"input-group-append\">\n                        <button class=\"btn btn-outline-secondary\"\n                            data-toggle=\"modal\"\n                            data-target=\"#staticBackdropClient\"\n                        >\n                            <i class=\"fas fa-plus-square\"></i> NOVO\n                        </button>\n                    </div>\n                </div>\n\n                <div style=\"height: 250px;\" class=\"table-responsive\">\n                    <table class=\"table table-condensed table-dark table-striped table-hover table-borderless align-middle table-head-fixed\">\n                        <thead>\n                            <tr>\n                                <th>Produtos</th>\n                                <th>Preo</th>\n                                <th>Quantidade</th>\n                                <th>Desconto</th>\n                                <th>Subtotal</th>\n                            </tr>\n                        </thead>\n                        <tbody>\n                            <tr data-toggle=\"modal\"\n                                data-target=\"#staticBackdrop\"\n                                v-for=\"product in order.products\" :key=\"product.identify\"\n                                @click.prevent=\"openDetailProduct(product, 'edit')\"\n                            >\n                                <td>{{product.title}}</td>\n                                <td>{{product.price | currency('R$ ', 2, {decimalSeparator: ','})}}</td>\n                                <td>{{product.qty}}</td>\n                                <td>{{ product.discount | currency('R$ ', 2, {decimalSeparator: ','}) }}</td>\n                                <td>\n                                    {{ sumSubtotal(product) | currency('R$ ', 2, {decimalSeparator: ','}) }}\n                                </td>\n                                <!-- <td title=\"Excluir item\" @click.prevent=\"openDetailProduct(product, 'remove')\">\n                                    <i class=\"fas fa-trash text-danger\"></i>\n                                </td> -->\n                            </tr>\n                        </tbody>\n                    </table>\n                </div>\n                <div class=\"row\">\n                    <div class=\"col-sm-4 col-6\">\n                        <div class=\"description-block border-right\">\n                            <h5 class=\"description-header text-warning\">\n                                {{order.total_discount | currency('R$ ', 2, {decimalSeparator: ','}) }}\n                            </h5>\n                            <span class=\"description-text\"> Desconto</span>\n                        </div>\n                        <!-- /.description-block -->\n                    </div>\n                    <!-- /.col -->\n                    <div class=\"col-sm-4 col-6\">\n                        <div class=\"description-block border-right\">\n                            <h5 class=\"description-header\"> {{total_items}} </h5>\n                            <span class=\"description-text\">Total de itens</span>\n                        </div>\n                        <!-- /.description-block -->\n                    </div>\n                    <!-- /.col -->\n\n                    <div class=\"col-sm-4 col-6\">\n                        <div class=\"description-block\">\n                            <h5 class=\"description-header text-success\"> {{round(order.total, 2) | currency('R$ ', 2, {decimalSeparator: ','}) }}</h5>\n                            <span class=\"description-text\">Total a pagar</span>\n                        </div>\n                        <!-- /.description-block -->\n                    </div>\n                    <div class=\"col-md-12\">\n                        <button v-if=\"order.products.length > 0 && client.length != ''\" class=\"btn btn-success btn-block\"\n                            data-toggle=\"modal\"\n                            data-target=\"#staticBackdropSale\"\n                        >\n                            <strong> <i class=\"fas fa-credit-card\"></i> Finalizar venda</strong>\n                        </button>\n\n                        <button v-else class=\"btn btn-success btn-block\" disabled title=\"Escolha o cliente e os produtos pimeiro!\">\n                            <strong> <i class=\"fas fa-credit-card\"></i> Finalizar venda</strong>\n                        </button>\n                    </div>\n                </div>\n                <!-- /.row -->\n            </div>\n            <!-- /.col -->\n            <div class=\"col-md-6\">\n                <div class=\"input-group col-md-12 mb-3 mt-4 mt-md-0\">\n                    <input @keyup=\"getProducts()\" v-model=\"search\" type=\"text\" class=\"form-control\" placeholder=\"Pesquise pelo nome, codigo ou descrio do produto\">\n                </div>\n\n                <ul class=\"products-list product-list-in-card pl-2 pr-2\">\n                    <li class=\"item\" v-for=\"(product, index) in products.data\" :key=\"index\"\n                        @click.prevent=\"openDetailProduct(product, 'add')\"\n                         data-toggle=\"modal\" data-target=\"#staticBackdrop\"\n                    >\n                        <div class=\"product-img\">\n                            <img v-if =\"product.image\" :src=\"product.image\" :alt='product.title'>\n                            <img v-else src=\"/images/company_none.png\" :alt='product.title'>\n                        </div>\n                        <div class=\"product-info\">\n                            <a href=\"#\" class=\"product-title\">\n                                {{product.title}}\n                                <span class=\"badge badge-light float-right\">\n                                    {{product.price | currency('R$ ', 2, {decimalSeparator: ','}) }}\n                                </span>\n                            </a>\n                            <span class=\"product-description\">\n                                {{product.description}}\n                            </span>\n                        </div>\n                    </li>\n                </ul>\n            </div>\n            <!-- /.col -->\n        </div>\n        <!-- /.row -->\n    </div>\n    <!-- ./card-body -->\n\n    <add-client @costumerSaved=\"costumerSaved\"></add-client>\n\n    <finalize-sale\n        :client=\"client\"\n        :order=\"order\"\n        @costumerSavedOrder=\"costumerSavedOrder\"\n    >\n    </finalize-sale>\n\n    <details-product\n        :product=\"product\"\n        :option=\"option\"\n        :titleModal=\"titleModal\"\n        @operationProductOrder=\"operationProductOrder\"\n    >\n    </details-product>\n</div>\n</template>\n<script>\nimport Vue2Filters from 'vue2-filters'\nimport FinalizeSale from './_partials/FinalizeSale.vue'\nimport DetailsProduct from './_partials/DetailsProduct.vue'\nimport AddClient from './_partials/AddClient.vue'\nimport Autocomplete from '@trevoreyre/autocomplete-vue'\n\nexport default {\n    mounted() {\n        this.getProducts()\n    },\n\n    data() {\n        return {\n            product: {\n                qty:  1,\n                identify: '',\n                title: '',\n                descprition: '',\n                image: '',\n                price: 0.00,\n                discount: 0.00,\n            },\n            products: [],\n            client: [],\n            order: {\n                products:[],\n                total_discount: 0.00,\n                total_paid: 0.00,\n                total_change: 0.00,\n                total: 0.00,\n                status: 'open',\n                address: '',\n                shipping: 0.00,\n                comment: '',\n                client: '',\n                table: '',\n                deliveryman: '',\n                form_payment_id: ''\n            },\n            total_items: 0,\n            option: 'add',\n            titleModal: 'Adicionar produto',\n            search: '',\n            searchClient: '',\n        }\n    },\n\n    methods: {\n        getProducts () {\n            axios.get(`/api/v1/products?filter=${this.search}`)\n                    .then(res => {\n                        this.products = res.data\n                    })\n                    .catch(err => {\n                        console.error('error', err.response)\n                    })\n        },\n\n        getClients(input) {\n            const url = `/api/v1/clients?filter=${input}`\n\n            return new Promise(resolve => {\n                if (input.length < 3) {\n                    return resolve([])\n                }\n\n                fetch(url)\n                .then(response => response.json())\n                .then(res => {\n                    resolve(res.data)\n                })\n                .catch(err => console.error(err))\n            })\n\n        },\n\n        getResultValue(result) {\n            return result.name\n        },\n\n        handleSubmit(result) {\n            this.client = result\n            console.log(result)\n            this.order.client = result.identify\n        },\n\n        costumerSaved (client) {\n            this.client = client\n        },\n\n        costumerSavedOrder () {\n            this.resetOrder()\n            this.resetClient()\n            // this.resetProducts()\n            this.resetProduct()\n        },\n\n        openDetailProduct (product, option) {\n            this.option = option\n            this.titleModal = option == 'add' ? \"Adicionar produto\" : \"Editar produto\"\n            product.qty = product.qty ? product.qty : 1\n            product.discount = product.discount ? product.discount : 0.00\n            this.product = product\n        },\n\n        operationProductOrder (products) {\n            this.order.products = products\n            this.order.total_discount = products.reduce((n, {discount}) => n + discount, 0)\n\n            let totalPrice = products.reduce((n, p) => n + (p.qty * p.price - p.discount), 0)\n\n            this.total_items = products.reduce((n, {qty}) => n + qty, 0)\n\n            this.order.total = totalPrice + this.order.shipping\n\n            this.order.total_paid = this.order.total + this.order.shipping - this.order.total_discount\n\n            this.order.total_change = this.order.total_paid - this.order.total\n        },\n\n        sumSubtotal (product) {\n            return this.round(product.price * product.qty - product.discount, 2)\n        },\n\n        round (num, places) {\n            if (!(\"\" + num).includes(\"e\")) {\n                return +(Math.round(num + \"e+\" + places)  + \"e-\" + places);\n            } else {\n                let arr = (\"\" + num).split(\"e\");\n                let sig = \"\"\n                if (+arr[1] + places > 0) {\n                    sig = \"+\";\n                }\n\n                return +(Math.round(+arr[0] + \"e\" + sig + (+arr[1] + places)) + \"e-\" + places);\n            }\n        },\n\n        resetOrder () {\n            this.order = {\n                products:[],\n                total_discount: 0.00,\n                total_paid: 0.00,\n                total_change: 0.00,\n                total: 0.00,\n                status: 'open',\n                address: '',\n                shipping: 0.00,\n                comment: '',\n                client: '',\n                table: null,\n                deliveryman: null,\n                form_payment_id: ''\n            }\n        },\n\n        resetClient () {\n            this.client = []\n        },\n\n        resetProducts () {\n            this.products = []\n        },\n\n        resetProduct () {\n            this.product = {\n                qty:  1,\n                identify: '',\n                title: '',\n                descprition: '',\n                image: '',\n                price: 0.00,\n                discount: 0.00,\n            }\n        }\n    },\n\n    mixins: [Vue2Filters.mixin],\n\n    components: {\n        FinalizeSale,\n        DetailsProduct,\n        AddClient,\n        Autocomplete\n    }\n}\n</script>\n\n<style>\n.form-control-input {\n  border: none;\n  width: 100%;\n  padding: 0px 0px 0px 48px;\n  margin: 0px;\n  box-sizing: inherit;\n  position: relative;\n  font-size: 16px;\n  line-height: 1.5;\n  flex: 1;\n  color: #fff;\n  background-color: transparent;\n  background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgZmlsbD0ibm9uZSIgc3Ryb2tlPSIjNjY2IiBzdHJva2Utd2lkdGg9IjIiIHN0cm9rZS1saW5lY2FwPSJyb3VuZCIgc3Ryb2tlLWxpbmVqb2luPSJyb3VuZCI+PGNpcmNsZSBjeD0iMTEiIGN5PSIxMSIgcj0iOCIvPjxwYXRoIGQ9Ik0yMSAyMWwtNC00Ii8+PC9zdmc+');\n  background-repeat: no-repeat;\n  background-position: 12px left;\n}\n\n.form-control-input:focus,\n.form-control-input[aria-expanded=\"true\"] {\n  border: none;\n  color: #fff;\n  outline: none;\n  /* box-shadow: 0 2px 2px rgba(0, 0, 0, 0.16); */\n}\n\n[data-position=\"below\"] .form-control-input[aria-expanded=\"true\"] {\n  border-bottom-color: transparent;\n  /* border-radius: 8px 8px 0 0; */\n}\n\n[data-position=\"above\"] .form-control-input[aria-expanded=\"true\"] {\n  /* border-top-color: transparent;\n  border-radius: 0 0 8px 8px; */\n  z-index: 2;\n}\n\n/* Loading spinner */\n.form-control[data-loading=\"true\"]::after {\n  content: \"\";\n  border: 3px solid rgba(0, 0, 0, 0.12);\n  border-right: 3px solid rgba(0, 0, 0, 0.48);\n  border-radius: 20%;\n  width: 20px;\n  height: 20px;\n  position: absolute;\n  right: 12px;\n  top: 50%;\n  transform: translateY(-50%);\n  animation: rotate 1s infinite linear;\n}\n\n.form-control-result-list {\n  margin: -13px;\n  width: 85%;\n  border: 1px solid rgba(0, 0, 0, 0.12);\n  padding: 0;\n  box-sizing: border-box;\n  max-height: 296px;\n  overflow-y: auto;\n  background: #fff;\n  list-style: none;\n  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.16);\n}\n\n[data-position=\"below\"] .form-control-result-list,\n[data-position=\"above\"] .form-control-result-list  {\n  margin-top: -1px;\n  border-top-color: transparent;\n  border-radius: 0 0 2px 2px;\n  padding-bottom: 1px;\n}\n\n/* [data-position=\"above\"] .form-control-result-list {\n  margin-bottom: -1px;\n  border-bottom-color: transparent;\n  border-radius: 8px 8px 0 0;\n  padding-top: 4px;\n} */\n\n/* Single result item */\n.form-control-result {\n  color: #000;\n  cursor: default;\n  padding: 12px 12px 12px 48px;\n  background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgZmlsbD0ibm9uZSIgc3Ryb2tlPSIjY2NjIiBzdHJva2Utd2lkdGg9IjIiIHN0cm9rZS1saW5lY2FwPSJyb3VuZCIgc3Ryb2tlLWxpbmVqb2luPSJyb3VuZCI+PGNpcmNsZSBjeD0iMTEiIGN5PSIxMSIgcj0iOCIvPjxwYXRoIGQ9Ik0yMSAyMWwtNC00Ii8+PC9zdmc+');\n  background-repeat: no-repeat;\n  background-position: 12px center;\n}\n\n.form-control-result:hover,\n.form-control-result[aria-selected=\"true\"] {\n  background-color: rgba(0, 0, 0, 0.06);\n}\n</style>\n"],"sourceRoot":""}]);
